              <div class="form-floating mb-3">
                <div class="row row-cols-2">
                  <div class="col">
                    <select
                      class="form-select"
                      aria-label="Default select example"
                      v-model="tempData.category"
                    >
                      <option selected disabled>分類</option>
                      <template v-for="(item, index) in categoryData" :key="index">
                        <option :value="item.title">{{ item.title }}</option>
                      </template>
                    </select>
                  </div>
                  <div class="col">
                    <select
                      class="form-select"
                      aria-label="Default select example2"
                      v-model="tempData.type"
                    >
                      <option selected disabled>類別</option>
                      <template v-for="(item, index) in typeData" :key="index">
                        <option :value="item">{{ item }}</option>
                      </template>
                    </select>
                  </div>
                </div>
              </div>
              <div class="form-floating mb-3">
                <textarea
                  class="form-control"
                  placeholder="Leave a comment here"
                  id="floatingTextarea2"
                  style="height: 100px"
                  v-model="tempData.comments"
                ></textarea>
                <label for="floatingTextarea2">Comments</label>
              </div>
              <!-- 下方 -->
              <template v-for="(item, index) in tempData.products" :key="index">
                <div class="row g-1 mb-2">
                  <div class="col-11 d-flex row row-cols-3 g-1">
                    <div class="col">
                      <div class="form-floating">
                        <input
                          type="email"
                          class="form-control"
                          id="floatingInputGrid"
                          v-model="tempData.products[index].size"
                        />
                        <label for="floatingInputGrid">Size</label>
                      </div>
                    </div>
                    <div class="col">
                      <div class="form-floating">
                        <input
                          type="email"
                          class="form-control"
                          id="floatingInputGrid"
                          v-model.number="tempData.products[index].price"
                        />
                        <label for="floatingInputGrid">Price</label>
                      </div>
                    </div>
                    <div class="col">
                      <div class="form-floating">
                        <input
                          type="email"
                          class="form-control"
                          id="floatingInputGrid"
                          v-model.number="tempData.products[index].stock"
                        />
                        <label for="floatingInputGrid">庫存</label>
                      </div>
                    </div>
                  </div>
                  <div class="col-1 align-self-center">
                    <button class="btn btn-outline-danger p-1" @click="deleteArray(index)">
                      <i class="bi bi-x-lg"></i>
                    </button>
                  </div>
