              </template>
              <button class="btn btn-dark w-100" @click="addArray">
                新增規格<i class="bi bi-plus"></i>
              </button>
            </div>
            <div class="col">
              <div class="row mb-3">
                <div class="col">
                  <img src="https://dummyimage.com/150x150/000/fff" alt="" />
                </div>
                <div class="col">
                  <img src="https://dummyimage.com/150x150/b33/fff" alt="" />
                </div>
                <div class="col">
                  <img src="https://dummyimage.com/150x150/c8c8c8/fff" alt="" />
                </div>
