        </div>
        <div class="modal-footer d-flex justify-content-between">
          <div class="form-check form-switch">
            <input
              class="form-check-input"
              type="checkbox"
              role="switch"
              id="isEnable"
              v-model="tempData.is_enable"
            />
            <label class="form-check-label" for="isEnable"
              >是否啟用</label
            >
          </div>
          <button type="button" class="btn btn-primary">新增產品</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import data from '@/assets/jsonData/category.json';
import { Modal } from 'bootstrap';

export default {
  data() {
    return {
      modal: '',
      categoryData: data.category,
      typeData: [],
      tempData: {
        title: '',
        category: '分類',
        type: '類別',
        comments: '',
        products: [
          {
            size: '8kg',
            price: 9999,
            stock: 9,
          },
        ],
        image: '',
        images: [],
        is_enable: false,
      },
    };
  },
  watch: {
    // eslint-disable-next-line object-shorthand
    'tempData.category'(newValue) {
      this.categoryData.forEach((element) => {
        if (newValue === element.title) {
          this.typeData = element.type;
        }
      });
    },
  },
  methods: {
    modalShow() {
      this.modal.show();
    },
    modalHide() {
      this.modal.hide();
    },
    deleteArray(index) {
      this.tempData.products.splice(index, 1);
      console.log(this.tempData.products);
    },
    addArray() {
      this.tempData.products.push({});
      console.log(this.tempData.products);
    },
  },
  mounted() {
    this.modal = new Modal(this.$refs.editAdd);
    this.modalShow();
  },
};
</script>
